<script lang="ts" setup>
/**
 * 图片组件
 *
 * @author libin
 * @date 2024年06月15日 11:58:57
 */

import { Ref, onMounted, ref } from 'vue'

const { flat } = withDefaults(
    defineProps<{
        flat?: number
    }>(),
    { flat: 100 }
)

const emiter = defineEmits<{
    (e: 'loaded'): void
    (e: 'completed'): void
}>()
/** 图片加载状态 */
const status: Ref<'load' | 'flat' | 'loaded' | 'fail'> = ref('load')
/** 初始加载时间 */
let start: number = Date.now()

/** 图片资源加载完成事件 */
const onLoad = (): void => {
    status.value = flat && Date.now() - start < flat ? 'loaded' : 'loaded'
    emiter('loaded')
    emiter('completed')
}

/** 图片资源加载失败事件 */
const onError = (): void => {
    status.value = 'fail'
    emiter('completed')
}

onMounted((): void => {
    start = Date.now()
})
</script>

<template>
    <img v-bind="$attrs" :class="['image', status]" @load="onLoad" @error="onError" />
</template>

<style lang="less" scoped>
.image.load {
    opacity: 0;
}
.image.flat,
.image.loaded {
    opacity: 1;
}
.image.loaded {
    transition: opacity 0.15s linear;
}
.image.fail {
    // 失败展示内容, 暂不处理~
    opacity: 1;
}
</style>
